<template>
    <div class="payment-buttons" :class="{'was-validated': !!errors.amount}">

        <div class="payment-buttons-grid mb-2">
            <btn type="button"
                outline
                variant="success"
                v-for="amount in amounts"
                v-html="`$${amount}`"
                :key="amount"
                :active="value ? value.toString() === amount.toString() : false"
                @click="onClickButton(amount)"/>
        </div>

        <input-group prepend="$" :class="{'is-invalid': !!errors.amount, 'mb-3': !page.site.recurring}">
            <input-field
                custom
                label="Other Amount"
                placeholder="Other Amount"
                :group="false"
                :value="value"
                @input="value => $emit('input', value)"/>
        </input-group>

        <form-feedback v-if="errors.amount" v-html="errors.amount.join('<br>')" class="d-block" invalid/>
    </div>
</template>

<script>
import Btn from 'vue-interface/src/Components/Btn';
import FormControl from 'vue-interface/src/Mixins/FormControl';
import InputField from 'vue-interface/src/Components/InputField';
import InputGroup from 'vue-interface/src/Components/InputGroup';
import FormFeedback from 'vue-interface/src/Components/FormFeedback';

export default {

    name: 'payment-buttons',

    mixins: [
        FormControl
    ],

    components: {
        Btn,
        InputField,
        InputGroup,
        FormFeedback
    },

    props: {

        page: {
            type: Object,
            required: true
        },

        amounts: {
            type: Array,
            required: true
        }

    },

    methods: {

        onClickButton(value) {
            this.$emit('input',
                parseFloat(this.value) !== (value = parseFloat(value)) ? value : null
            );
        }

    }

};
</script>
